.testimonilas {
  @include contentPaddingInline;

  background-image: url(../images/image-mobile/testimonilas-image.png);
  background-position: top;
  background-repeat: no-repeat;
  padding-top: 50px;

  @include onTablet {
    background-image: url(../images/image-tablet/testimonilas-image-tablet.png);
  }

  @include onDesktop {
    background-image: url(../images/image-desktop/testimonilas-image-desktop.png);
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__image {
    margin-top: 30px;

    @include onTablet {
      margin-top: 40px;
    }

    @include onDesktop {
      margin-top: 30px;
    }

    @include hover(transform, scale(1.1));
  }

  &__name {
    color: $main-text-color;
    text-align: center;
    font-size: 16px;
    font-weight: 700;
    line-height: 22px;

    margin-top: 10px;
  }

  &__position {
    color: $acticle-text-color;
    text-align: center;
    font-size: 14px;
    font-weight: 400;
    line-height: 21px;

    margin-top: 5px;
  }
}
